import { DomShape } from '@app/util/dom-canvas/dom-canvas.model';
import { LinkedNode } from '@app/model/linkednode.model';

export class NodeModel {
  public id: String;
  public label: String;
  public shape: String;
  public color: String;
  public createdAt: Date;
  public updatedAt: Date;
  public idMindMap: String;
  public x: Number;
  public y: Number;
  public width: Number;
  public height: Number;
  public textColor: String;
  public relations: NodeModel[];
  public linkedNodesReference: LinkedNode[];

  constructor();
  constructor() {
    this.relations = [];
    this.linkedNodesReference = [];
  }

  public static parseDomShape(
    shape: DomShape,
    withConnections = true
  ): NodeModel {
    const node = new NodeModel();

    node.id = shape.id;
    node.height = shape.height;
    node.width = shape.width;
    node.color = shape.colorCode;
    node.textColor = shape.textColor;
    node.label = shape.label;
    node.x = shape.xAxis;
    node.y = shape.yAxis;

    if (shape.connections && withConnections) {
      shape.connections.forEach(conn => {
        node.relations.push(this.parseDomShape(conn, false));
      });
    }

    return node;
  }

  public toDomShape(): DomShape {
    const shape = new DomShape();

    shape.build(
      this.x,
      this.y,
      this.width,
      this.height,
      this.color,
      this.label,
      this.textColor,
      this.id
    );

    this.relations.forEach(rel => {
      const parsed = rel.toDomShape();
      shape.connections.push(parsed);
    });
    // if (this.linkedNodes && this.linkedNodes.length > 0) {
    //   this.linkedNodes.forEach(linked => {
    //     shape.connections.push(linked.toDomShape());
    //   });
    // }

    return shape;
  }

  public getConnectedNodes(list: NodeModel[]) {
    this.linkedNodesReference.forEach(linked => {
      if (linked.id) {
        this.relations.push(
          list.find(item => item.id === linked.referenceNode)
        );
      }
    });
  }

  public setReferencedNodes() {
    this.relations.forEach(linked => {
      const alreadyExist = this.linkedNodesReference.find(
        item =>
          (item.referenceNode === this.id && item.neighborNode === linked.id) ||
          (item.neighborNode === this.id && item.referenceNode === linked.id)
      );

      if (!alreadyExist) {
        const reference = new LinkedNode();
        reference.referenceNode = this.id;
        reference.neighborNode = linked.id;
        this.linkedNodesReference.push(reference);
      }
    });
  }
}



