import { environment } from '../../environments/environment';

export class ReadOnlyUtil {
  public static readonly RequestContentTypes = {
    FORM_URL_ENCODED: 'application/x-www-form-urlencoded',
    MULTIPART_FORM_DATA: 'multipart/form-data',
    APPLICATION_JSON: 'application/json'
  };

  public static readonly RequestPaths = {
    SERVER_ROOT: environment.server,
    SERVER_FILES: environment.files,
    PROJECT: 'projects',
    STUDENT: 'students',
    CLASS: 'classes',
    MONITOR: 'monitors',
    MINDMAP: 'mindmap',
    PHOTOBOARD: 'photoboards',
    IMAGE: 'images'
  };

  private constructor () {}
}



