import { DomCanvasEvents } from '@app/util/dom-canvas/dom-canvas-events';

export class DomCanvas {
  // Inspirado em https://github.com/simonsarris/Canvas-tutorials/blob/master/shapes.js
  public canvas: HTMLCanvasElement;
  public context: CanvasRenderingContext2D;
  public width: number;
  public height: number;
  public state: State;
  public eventsCallback: DomCanvasEvents;
  public isInitialized: boolean;

  /**
   * TODO refatorar toda a classe para remover duplicidades
   */
  constructor(canvas: HTMLCanvasElement) {
    this.canvas = canvas;
    this.context = canvas.getContext('2d');
    this.width = canvas.width;
    this.height = canvas.height;
    this.state = new State();
    this.setupEvents();
  }

  public getRandomInt(min, max) {
    /**
     * Original: https://developer.mozilla.org/pt-BR/docs/Web/JavaScript/Reference/Global_Objects/Math/random
     */
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min)) + min;
  }

  public clearCanvas(context: CanvasRenderingContext2D = this.context) {
    context.clearRect(0, 0, this.width, this.height);
    this.state.shapes.forEach(s => {
      s.border.isBorderShown = false;
      s.connectButton.isConnectorShown = false;
    });
  }

  public clearAll() {
    this.clearCanvas();
    this.state.shapes.length = 0;
  }

  public clearShape(
    shape: DomShape,
    context: CanvasRenderingContext2D = this.context
  ) {
    shape.border.removeShapeBorder(context);
    shape.connectButton.removeConnector(context);
    shape.removeButton.removeConnector(context);
    context.clearRect(shape.xAxis, shape.yAxis, shape.width, shape.height);
  }

  public removeShape(
    shape: DomShape,
    context: CanvasRenderingContext2D = this.context
  ) {
    shape.border.removeShapeBorder(context);
    shape.connectButton.removeConnector(context);
    shape.removeButton.removeConnector(context);
    this.clearShape(shape);

    if (shape === this.state.selection) {
      this.state.selection = null;
    }

    this.state.shapes.forEach(s => {
      if (s.connections.includes(shape)) {
        this.removeFromArray(shape, s.connections);
      }
    });

    this.removeFromArray(shape, this.state.shapes);
    this.clearCanvas();
    this.drawAll();
  }

  public removeFromArray(itemToRemove: any, array: any[]) {
    const indexToRemove = array.indexOf(itemToRemove);
    array.splice(indexToRemove, 1);
  }

  public drawShape(
    shape: DomShape,
    context: CanvasRenderingContext2D = this.context
  ) {
    context.fillStyle = shape.colorCode.toString();
    context.strokeStyle = shape.colorCode.toString();
    const cornerRadius = shape.cornerRadius;

    context.lineJoin = 'round';
    context.lineWidth = cornerRadius;

    context.strokeRect(
      shape.xAxis + cornerRadius / 2,
      shape.yAxis + cornerRadius / 2,
      shape.width - cornerRadius,
      shape.height - cornerRadius
    );

    context.fillRect(
      shape.xAxis + cornerRadius / 2,
      shape.yAxis + cornerRadius / 2,
      shape.width - cornerRadius,
      shape.height - cornerRadius
    );

    const middleX = shape.width / 2;
    const connector = new DomShapeConnector();
    connector.xAxis = shape.xAxis + middleX - middleX / 4;
    connector.yAxis = shape.yAxis + 5 + shape.height;
    connector.height = 32;
    connector.width = 32;
    connector.iconPath = 'assets/images/icons/add_shape_icon.png';
    connector.onActionClick = () => {
      this.eventsCallback.addNewNode(
        this.getRandomInt(0, this.width),
        this.getRandomInt(0, this.height)
      );
    };

    const removable = new DomShapeConnector();
    removable.xAxis = shape.xAxis + middleX + middleX / 4;
    removable.yAxis = shape.yAxis + 5 + shape.height;
    removable.height = 32;
    removable.width = 32;
    removable.iconPath = 'assets/images/icons/remove_shape_icon.png';
    removable.onActionClick = () => {
      this.eventsCallback.removeNode(shape);
      this.removeShape(shape);
    };

    shape.connectButton = connector;
    shape.removeButton = removable;

    if (shape.label) {
      context.fillStyle = shape.textColor.toString();
      context.textAlign = 'center';
      context.font = '14pt Roboto';
      context.fillText(
        shape.label,
        shape.xAxis + shape.width / 2,
        shape.yAxis + shape.height / 2,
        shape.width
      );
    }
  }

  public linkShapes(
    originShape: DomShape,
    targetShape: DomShape,
    repaintOrigin = false,
    context: CanvasRenderingContext2D = this.context
  ) {
    const originMiddle = originShape.getConnectionPoints();
    const targetMiddle = targetShape.getConnectionPoints();

    context.lineWidth = 2;
    context.strokeStyle = '#cccccc';
    context.beginPath();
    context.moveTo(originMiddle.center.x, originMiddle.center.y);
    context.lineTo(targetMiddle.center.x, targetMiddle.center.y);
    context.closePath();
    context.stroke();

    if (repaintOrigin) {
      this.clearShape(originShape);
      this.drawShape(originShape);
    }
  }

  public drawAll(state: State = this.state) {
    if (state && state.shapes) {
      state.shapes.forEach(s => {
        s.connections.forEach(c => {
          const toConnect = state.shapes.find(i => {
            if (c.id && i.id) {
              return i.id === c.id;
            } else {
              return i.label === c.label;
            }
          });
          this.linkShapes(s, toConnect);
        });
      });

      state.shapes.forEach(s => {
        if (s === state.selection) {
          s.border.drawShapeBorder(this.context);
        }
        this.drawShape(s);
        if (s === state.selection) {
          s.connectButton.drawIcon(this.context);
          s.removeButton.drawIcon(this.context);
        }
      });
    }
  }

  public restore() {
    this.context.restore();
  }

  protected getMousePosition(canvas: HTMLCanvasElement, mouseEvent) {
    const bounds = canvas.getBoundingClientRect();
    const scaleX = canvas.width / bounds.width;
    const scaleY = canvas.height / bounds.height;

    return {
      x: (mouseEvent.clientX - bounds.left) * scaleX,
      y: (mouseEvent.clientY - bounds.top) * scaleY
    };
  }

  private setupEvents() {
    this.canvas.addEventListener(
      'mousedown',
      e => {
        const mouse = this.getMousePosition(this.canvas, e);
        const mx = mouse.x;
        const my = mouse.y;
        const shapes = this.state.shapes;

        if (
          this.state.selection &&
          !this.state.selection.contains(mx, my) &&
          !this.state.selection.removeButton.contains(mx, my) &&
          !this.state.selection.connectButton.contains(mx, my)
        ) {
          this.state.selection.border.removeShapeBorder(this.context);
          this.state.selection.connectButton.removeConnector(this.context);
          this.state.selection.removeButton.removeConnector(this.context);
          this.drawShape(this.state.selection);
          this.state.selection = null;
        }

        if (
          this.state.selection &&
          this.state.selection.connectButton.contains(mx, my)
        ) {
          this.state.selection.connectButton.onActionClick();
        } else if (
          this.state.selection &&
          this.state.selection.removeButton.contains(mx, my)
        ) {
          this.state.selection.removeButton.onActionClick();
        } else {
          shapes.filter(s => s.contains(mx, my)).forEach(mySel => {
            this.state.xDragOff = mx - mySel.xAxis;
            this.state.yDragOff = my - mySel.yAxis;
            this.state.selection = mySel;
            this.state.isValid = false;
            this.state.isDragging = true;
            this.state.selection.border.removeShapeBorder(this.context);
            this.clearShape(this.state.selection);
            this.state.selection.connectButton.removeConnector(this.context);
            this.state.selection.removeButton.removeConnector(this.context);
            this.state.selection.border.drawShapeBorder(this.context);
            this.drawShape(this.state.selection);
            this.state.selection.connectButton.drawIcon(this.context);
            this.state.selection.removeButton.drawIcon(this.context);
          });
        }
      },
      true
    );

    this.canvas.addEventListener(
      'dblclick',
      e => {
        const mouse = this.getMousePosition(this.canvas, e);
        const mx = mouse.x;
        const my = mouse.y;
        const shapes = this.state.shapes;

        const interserctions = shapes.filter(s => s.contains(mx, my));

        if (interserctions.length === 0) {
          this.eventsCallback.addNewNode(mx, my);
        }
      },
      true
    );

    this.canvas.addEventListener(
      'selectstart',
      e => {
        e.preventDefault();
        return false;
      },
      false
    );

    this.canvas.addEventListener(
      'mousemove',
      e => {
        const mouse = this.getMousePosition(this.canvas, e);

        if (this.state.isDragging && this.state.selection) {
          this.state.selection.xAxis = mouse.x - this.state.xDragOff;
          this.state.selection.yAxis = mouse.y - this.state.yDragOff;
          this.state.selection.setConnectorCoordinates();
          this.state.selection.setBorderCoordinates();
          this.state.isValid = false; // Something's dragging so we must redraw
          this.clearCanvas();
          this.drawAll();
        }
      },
      true
    );
    this.canvas.addEventListener(
      'mouseup',
      e => {
        this.state.isDragging = false;
      },
      true
    );
  }
}

class State {
  public isValid: Boolean = false;
  public isDragging: Boolean = false;
  public shapes: DomShape[] = [];
  public selection: DomShape = null;
  public xDragOff: number;
  public yDragOff: number;
  public xConnectorDragOff: number;
  public yConnectorDragOff: number;

  public addShape(shape: DomShape) {
    this.shapes.push(shape);
    this.isValid = false;
  }
}

export class DomShape {
  public id: String; // creation timestamp in milliseconds.
  public xAxis: number;
  public yAxis: number;
  public width: number;
  public height: number;
  public label: string;
  public cornerRadius: number;
  public colorCode: String;
  public textColor: String;
  public connectButton: DomShapeConnector;
  public removeButton: DomShapeConnector;
  public border: DomShapeBorder;
  public connections: DomShape[];

  constructor() {}

  public build(x, y, w, h, color, label?, textColor?, id?): DomShape {
    this.xAxis = x;
    this.yAxis = y;
    this.width = w;
    this.height = h;
    this.id = id || '';
    this.colorCode = color;
    this.connections = [];
    this.label = label || '';
    this.cornerRadius = 20; // Default value
    this.textColor = textColor ? textColor : '#000000'; // Default value
    this.border = new DomShapeBorder(
      this.xAxis,
      this.yAxis,
      this.width,
      this.height,
      this.cornerRadius + 5
    );
    return this;
  }

  public setBorder(context: CanvasRenderingContext2D) {}

  public getConnectionPoints() {
    // Middle point
    const xCenter = this.xAxis + this.width / 2;
    const yCenter = this.yAxis + this.height / 2;
    const center = { x: xCenter, y: yCenter };
    // Left Side Center
    const xCenterLeft = this.xAxis;
    const yCenterLeft = yCenter;
    const leftSideCenter = { x: xCenterLeft, y: yCenterLeft };

    // Right Side Center
    const xCenterRight = this.xAxis + this.width;
    const yCenterRight = yCenter;
    const rightSideCenter = { x: xCenterRight, y: yCenterRight };

    // Top Side Center
    const xCenterTop = xCenter;
    const yCenterTop = this.yAxis + this.height;
    const topSideCenter = { x: xCenterTop, y: yCenterTop };

    // Bottom Side Center
    const xCenterBottom = xCenter;
    const yCenterBottom = this.yAxis;
    const bottomSideCenter = { x: xCenterBottom, y: yCenterBottom };

    // Left Top Corner
    const xTopLeft = this.xAxis;
    const yTopLeft = yCenter + this.height;
    const leftTopCorner = { x: xTopLeft, y: yTopLeft };

    // Left Bottom Corner
    const xBottomLeft = this.xAxis;
    const yBottomLeft = this.yAxis;
    const leftBottomCorner = { x: xBottomLeft, y: yBottomLeft };

    // Right Top Corner
    const xTopRight = xCenterRight;
    const yTopRight = yCenterTop;
    const rightTopCorner = { x: xTopRight, y: yTopRight };

    // Right Bottom Corner
    const xBottomRight = xCenterRight;
    const yBottomRight = this.yAxis;
    const rightBottomCorner = { x: xBottomLeft, y: yBottomLeft };

    return {
      center,
      leftSideCenter,
      rightSideCenter,
      topSideCenter,
      bottomSideCenter,
      leftBottomCorner,
      rightBottomCorner,
      leftTopCorner,
      rightTopCorner
    };
  }

  public setConnectorCoordinates() {
    const middle = this.width / 2;
    this.connectButton.xAxis = this.xAxis + middle - middle / 4;
    this.connectButton.yAxis = this.yAxis + this.height / 2;

    this.removeButton.xAxis = this.xAxis + middle + middle / 4;
    this.removeButton.yAxis = this.yAxis + this.height / 2;
  }

  public setBorderCoordinates() {
    this.border.xAxis = this.xAxis - 2;
    this.border.yAxis = this.yAxis - 2;
  }
  public contains(clickX, clickY): boolean {
    return (
      this.xAxis <= clickX &&
      this.xAxis + this.width >= clickX &&
      this.yAxis <= clickY &&
      this.yAxis + this.height >= clickY
    );
  }
}

export class DomShapeBorder extends DomShape {
  public isBorderShown: boolean;

  constructor(x, y, w, h, cornerRadius) {
    super();
    this.cornerRadius = cornerRadius;
    this.xAxis = x - 2;
    this.yAxis = y - 2;
    this.width = w + 5;
    this.height = h + 5;
    this.colorCode = '#80778899';
  }

  public removeShapeBorder(
    context: CanvasRenderingContext2D,
    shape: DomShape = this
  ) {
    context.clearRect(
      shape.xAxis - 1,
      shape.yAxis - 1,
      shape.width + 2,
      shape.height + 2
    );
    this.isBorderShown = false;
  }

  public drawShapeBorder(
    context: CanvasRenderingContext2D,
    shape: DomShape = this
  ) {
    context.fillStyle = shape.colorCode.toString();
    context.strokeStyle = shape.colorCode.toString();
    const cornerRadius = shape.cornerRadius;

    context.lineJoin = 'round';
    context.lineWidth = cornerRadius;

    if (this.isBorderShown) {
      return;
    } else {
      context.strokeRect(
        shape.xAxis + cornerRadius / 2,
        shape.yAxis + cornerRadius / 2,
        shape.width - cornerRadius,
        shape.height - cornerRadius
      );

      context.fillRect(
        shape.xAxis + cornerRadius / 2,
        shape.yAxis + cornerRadius / 2,
        shape.width - cornerRadius,
        shape.height - cornerRadius
      );

      this.isBorderShown = true;
    }
  }
}

export interface ConnectorCoordinate {
  x: number;
  y: number;
}

export type DomActionClickListener = () => void;

export class DomShapeConnector extends DomShape {
  public isConnectorShown: boolean;
  public isConnectorDragging: boolean;
  public iconPath: String;
  public onActionClick: DomActionClickListener;

  public drawConnector(context: CanvasRenderingContext2D) {
    if (this.isConnectorShown) {
      return;
    } else {
      context.fillStyle = '#80778899';
      context.lineWidth = 1;
      context.font = 'bold 10pt Roboto';

      context.fillRect(this.xAxis, this.yAxis, this.width, this.height);

      this.isConnectorShown = true;
    }
  }

  public drawIcon(context: CanvasRenderingContext2D, shape = this) {
    const imageObj = new Image();
    imageObj.onload = function() {
      context.drawImage(
        imageObj,
        shape.xAxis,
        shape.yAxis,
        shape.width,
        shape.height
      );
    };
    imageObj.src = this.iconPath
      ? this.iconPath.toString()
      : 'assets/images/icons/add_node_icon.png';

    shape.isConnectorShown = true;
  }

  public removeConnector(context: CanvasRenderingContext2D) {
    if (this.isConnectorShown) {
      context.clearRect(
        this.xAxis,
        this.yAxis,
        this.width + 1,
        this.height + 1
      );
    }
    this.isConnectorShown = false;
  }
}



