import { Injectable } from '@angular/core';
import { Project } from '@app/model/project.model';
import { Student } from '@app/model/student.model';
import { DomApiService } from '@app/util/dom-api/dom-api.service';
import { ReadOnlyUtil } from '@app/util/read-only-util';
import { ProjectParser } from '@app/model/parser/project.parser';

@Injectable()
export class SessionService {
  private loggedUser: Student;
  private selectedProject: Project;

  constructor(public domApi: DomApiService) {}

  public getSelectedProject() {
    return this.selectedProject;
  }

  public setSelectedProject(project: Project) {
    this.selectedProject = project;
  }

  public getLoggedUser() {
    return this.loggedUser;
  }

  public setLoggedUser(user: Student) {
    this.loggedUser = user;
  }

  //#region  Requests

  public getProjects() {
    return this.domApi.get(
      `${ReadOnlyUtil.RequestPaths.SERVER_ROOT}/${
        ReadOnlyUtil.RequestPaths.PROJECT
      }`
    );
  }

  public updateProject(project: Project) {
    const body = ProjectParser.parseToJson(project);

    return this.domApi.patch(
      `${ReadOnlyUtil.RequestPaths.SERVER_ROOT}/${
        ReadOnlyUtil.RequestPaths.PROJECT
      }/p/${project.id}`,
      JSON.stringify(body)
    );
  }

  public getProjectById(id: string) {
    return this.domApi.get(
      `${ReadOnlyUtil.RequestPaths.SERVER_ROOT}/${
        ReadOnlyUtil.RequestPaths.PROJECT
      }/${id}`
    );
  }

  //#endregion
}



