import { Project } from '@app/model/project.model';
import { StudentParser } from '@app/model/parser/student.parser';
import { OpportunityParser } from '@app/model/parser/opportunity.parser';
import { MindMapParser } from '@app/model/parser/mindmap.parser';

export class ProjectParser {
  public static parse(json: any): Project {
    const obj = new Project();

    if (json) {
      obj.id = json.id ? json.id : '';
      obj.title = json.title ? json.title : '';
      obj.description = json.description ? json.description : '';
      obj.startDate = json.start_date ? new Date(json.start_date) : new Date();
      obj.endDate = json.end_date ? new Date(json.end_date) : new Date();
      obj.student = StudentParser.parse(json.Student);
      obj.opportunity = OpportunityParser.parse(json.Opportunity);
      obj.mindMap = MindMapParser.parse(json.MindMap);
      obj.createdAt = json.createdAt || '';
      obj.updatedAt = json.updatedAt || '';
    } else {
      throw new Error('Project - Não foi possível realizar a conversão.');
    }

    return obj;
  }

  // TODO
  public static parseToJson(object: Project): any {
    const json = {};
    if (object) {
      json['id'] = object.id ? object.id : '';
      json['title'] = object.title ? object.title : '';
      json['description'] = object.description ? object.description : '';
      json['start_date'] = object.startDate.toISOString();
      json['end_date'] = object.endDate.toISOString();
      json['Student'] = StudentParser.parseToJson(object.student);
      json['Opportunity'] = OpportunityParser.parseToJson(object.opportunity);
      json['MindMap'] = MindMapParser.parseToJson(object.mindMap);
      json['Audios'] = [];
      json['PDFs'] = [];
      json['PhotoBoards'] = [];
      json['Sketches'] = [];
      json['createdAt'] = object.createdAt;
      json['updatedAt'] = object.updatedAt;
    } else {
      throw new Error('Project - Não foi possível realizar a conversão JSON.');
    }

    return json;
  }
}



