import { Student } from '@app/model/student.model';
import { Opportunity } from '@app/model/opportunity.model';
import { MindMapModel } from '@app/model/mindmap.model';

export class Project {
  private _id: string;
  public get id(): string {
    return this._id;
  }
  public set id(v: string) {
    this._id = v;
  }

  private _title: string;
  public get title(): string {
    return this._title;
  }
  public set title(v: string) {
    this._title = v;
  }

  private _description: string;
  public get description(): string {
    return this._description;
  }
  public set description(v: string) {
    this._description = v;
  }

  private _startDate: Date;
  public get startDate(): Date {
    return this._startDate;
  }
  public set startDate(v: Date) {
    this._startDate = v;
  }

  private _endDate: Date;
  public get endDate(): Date {
    return this._endDate;
  }
  public set endDate(v: Date) {
    this._endDate = v;
  }

  private _student: Student;
  public get student(): Student {
    return this._student;
  }
  public set student(v: Student) {
    this._student = v;
  }

  private _opportunity: Opportunity;
  public get opportunity(): Opportunity {
    return this._opportunity;
  }
  public set opportunity(v: Opportunity) {
    this._opportunity = v;
  }

  private _mindMap: MindMapModel;
  public get mindMap(): MindMapModel {
    return this._mindMap;
  }
  public set mindMap(v: MindMapModel) {
    this._mindMap = v;
  }

  public createdAt: Date;
  public updatedAt: Date;

  public constructor() {}
}



