import { User } from '@app/model/user.model';
import { Class } from '@app/model/class.models';

export class Student extends User {
  private _class: Class;
  public get class(): Class {
    return this._class;
  }
  public set class(v: Class) {
    this._class = v;
  }

  constructor() {
    super();
  }
}



