import { NodeModel } from '@app/model/node.model';
import { DomShape } from '@app/util/dom-canvas/dom-canvas.model';
import { LinkedNodeParser } from '@app/model/parser/linkednode.parser';

export class NodeParser {
  public static parse(json: any, withConnections = true): NodeModel {
    const obj = new NodeModel();

    if (json) {
      obj.id = json.id || '';
      obj.label = json.label || '';
      obj.shape = json.shape || '';
      obj.color = json.color || '';
      obj.createdAt = json.createdAt || '';
      obj.updatedAt = json.updatedAt || '';
      obj.idMindMap = json.idMindMap || '';
      obj.x = json.x || 0;
      obj.y = json.y || 0;
      obj.width = json.width || 0;
      obj.height = json.height || 0;
      obj.textColor = json.textColor || '';

      if (json.relations && withConnections) {
        json.relations.forEach(linked => {
          obj.relations.push(this.parse(linked));
        });
      }
    } else {
      throw new Error('Node - Não foi possível realizar a conversão.');
    }

    return obj;
  }

  public static parseToJson(object: NodeModel, withConnections = true): any {
    const json = {};

    if (object.id && object.id !== '') {
      json['id'] = object.id;
    }
    json['label'] = object.label || '';
    json['shape'] = object.shape || '';
    json['color'] = object.color || '';
    json['id_mind_map'] = object.idMindMap || '';
    json['x'] = object.x || 0;
    json['y'] = object.y || 0;
    json['width'] = object.width || 0;
    json['height'] = object.height || 0;
    json['textColor'] = object.textColor || '';
    json['relations'] = [];

    if (object.relations && withConnections) {
      object.relations.forEach(linked => {
        json['relations'].push(this.parseToJson(linked));
      });
    }

    return json;
  }

  private static parseLinkedNodeToJson(
    reference: NodeModel,
    linked: NodeModel
  ) {}

  public static domShapeToModel(shape: DomShape): NodeModel {
    const obj = new NodeModel();

    if (shape) {
      obj.id = shape.id || '';
      obj.label = shape.label || '';
      obj.color = shape.colorCode || '';
      obj.x = shape.xAxis || 0;
      obj.y = shape.yAxis || 0;
      obj.width = shape.width || 0;
      obj.height = shape.height || 0;
      obj.textColor = shape.textColor || '';

      if (shape.connections) {
        shape.connections.forEach(linked => {
          obj.relations.push(this.domShapeToModel(linked));
        });
      }
    } else {
      throw new Error('Node - Não foi possível realizar a conversão.');
    }

    return obj;
  }
}



