import { MindMapModel } from '@app/model/mindmap.model';
import { NodeParser } from '@app/model/parser/node.parser';

export class MindMapParser {
  public static parse(json: any): MindMapModel {
    const obj = new MindMapModel();

    if (json) {
      obj.id = json.id ? json.id : '';
      obj.title = json.title ? json.title : '';
      obj.idProject = json.id_project ? json.id_project : '';
      obj.createdAt = json.createdAt || '';
      obj.updatedAt = json.updatedAt || '';
      if (json.Nodes) {
        json.Nodes.forEach(n => {
          obj.nodes.push(NodeParser.parse(n));
        });
      }
    } else {
      throw new Error('MindMap - Não foi possível realizar a conversão.');
    }

    return obj;
  }

  public static parseToJson(object: MindMapModel): any {
    const json = {};

    json['id'] = object.id;
    json['id_project'] = object.idProject;
    json['Nodes'] = [];
    json['title'] = object.title;
    json['createdAt'] = object.createdAt;
    json['updatedAt'] = object.updatedAt;

    if (object.nodes) {
      object.nodes.forEach(n => {
        json['Nodes'].push(NodeParser.parseToJson(n));
      });
    }

    return json;
  }
}



